@import url(https://fonts.googleapis.com/css2?family=Road+Rage&family=Roboto:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Road+Rage&family=Roboto:wght@300;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1 {
  font-family: "Lobster Two", cursive;
  text-align: center;
  font-size: 50px;
}
h2 {
  color: red;
}
body {
  font-family: "Lobster Two", cursive;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: rgb(19, 136, 200);
  background: linear-gradient(
    90deg,
    rgba(19, 136, 200, 1) 2%,
    rgba(2, 0, 36, 1) 6%,
    rgba(42, 42, 133, 1) 13%,
    rgba(0, 212, 255, 0.07466736694677867) 45%,
    rgba(1, 190, 241, 1) 59%
  );*/
}
.countries {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.country {
  display: flex;
  flex-direction: column;
  background-color: rgb(245, 240, 240);
  height: 250px;
  width: 250px;
  margin: 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  justify-content: center;
  position: relative;
  border-radius: 5%;
}

#about img {
  width: 25px;
  height: 25px;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
}

.country:hover {
  box-shadow: 0 0 20px #333;
  filter: grayScale(0%);
  cursor: pointer;
}
.cardContent {
  padding: 2px 16px;
}
input[type="text"] {
  padding: 6px;
  border: 3px solid black;
  margin-top: 8px;
  margin-right: 16px;
  font-size: 17px;
  height: 50px;
  width: 100%;
}
.statement {
  position: fixed;
  text-align: center;
  bottom: 0px;
  width: 100%;
}
.weather img {
  width: 200px;
  height: 200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-color: rgb(16, 154, 218);
  border-width: 0.02px;
  border-style: solid;
  border-radius: 50%;
}
footer {
  /*padding: 2rem;
  text-align: center;
  position: relative;*/
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}
header,
footer {
  background: rgb(0, 170, 255);
  background: linear-gradient(
    90deg,
    rgba(0, 170, 255, 0.07466736694677867) 0%,
    rgba(1, 161, 241, 1) 24%
  );
}
.main {
  overflow: auto;
  padding-bottom: 180px;

  height: 75vh;
  width: 100%;
  background: url(/countries/static/media/background.eb47d0bd.png) no-repeat fixed center center/cover;
}

